import './idp-selector.scss'
import React, {useState, useEffect} from 'react';
import {IdpResponseData, IdpResponseDataInitialState} from "../CommonInterfaces/idp-response-types";
import { useFlag } from '@unleash/proxy-client-react';
import IdpNotSetup from "../IdpNotSetup/idp-not-setup";
import PropTypes from "prop-types";
import {useChrome} from "@redhat-cloud-services/frontend-components/useChrome";
import {apiGetIdp} from '../ApiClient/idp-controller';
import {FlowStep} from '../Constants/flow-step';
import FlowTemplate from '../IdpFlowTemplate/flow-template';
import ConfiguredIdp from '../Configured/configured-idp';
import CreateOidcIdp from "../Creation/create-oidc-idp";
import CreateSamlIdp from "../Creation/create-saml-idp";
import {PageHeader, PageHeaderTitle} from "@redhat-cloud-services/frontend-components/PageHeader";
import {AlertGroup, Button, Card, PageSection, Text, TextContent, TextVariants} from "@patternfly/react-core";
import ExternalLinkSquareAltIcon from "@patternfly/react-icons/dist/esm/icons/external-link-alt-icon";
import messagesContext from "../CommonInterfaces/messages-context";
import DisabledPage from "../../../Components/DisabledPage/disabled-page";
import ChooseIdpType from "../IdpNotSetup/choose-idp-type";

interface Props {
    accessToken: string
}

const IdpSelector = ({accessToken}: Props) => {
    const chrome = useChrome();

    // has the admin's account been configured with an idp. If so display the management screen.
    const [isConfigured, setIsConfigured] = useState(false);
    const [isAllowedToConfigure, setIsAllowedToConfigure] = useState(false);
    const [isLoading, setIsLoading] = useState(true);
    const [isInit, setIsInit] = useState(true);
    const [idpData, setIdpData] = useState(IdpResponseDataInitialState);
    const [configFlow, setConfigFlow] = useState("");
    const [messages, setMessages] = useState([]);

    const isM3Enabled = useFlag('iamconsole.IDPIntegration.m3');
    const docsLink = "https://docs.redhat.com/en/documentation/red_hat_customer_portal/1/html/configuring_identity_provider_integration/index";

    const recomputeAppState = () => {
        apiGetIdp(chrome)
        .then(response => {
            if (response.status === 204) {
                return {status: ''};
            }
            return response.json()
        })
        .then((data: IdpResponseData) => {
            if (data.providerId === 'oidc-improved' || data.providerId === 'saml-improved') {
                setIdpData(data);
                setIsLoading(false);
                setIsConfigured(true);
            } else if (data.error) {
                setIsLoading(false);
                setIsConfigured(false);
                setIsAllowedToConfigure(false);
            } else {
                setIsLoading(false);
                setIsConfigured(false);
                setIsAllowedToConfigure(true);
            }
        })
        .catch(error => {
            console.error('Error: ', error);
            setIsLoading(false);
        })
    }

    useEffect(() => {
        recomputeAppState();
    }, [])

    /**
     * handles the correct configuration path to display. called directly in the jsx
     * and relies on the configFlow state
     *
     * Will need to update this method once the new card page is in place
     */
    const configPathChosen = () => {
        if (configFlow === FlowStep.SAML_CONFIG) {
            return <CreateSamlIdp chromeRef={chrome}/>
        } else if (configFlow === FlowStep.OIDC_CONFIG) {
            // return OIDC page
            return <CreateOidcIdp chromeRef={chrome}/>
        }

        return <div className="hiddenPage"></div>;
    }

    const configPathChosenInit = () => {
        if (isM3Enabled) {
            return (
                <Card>
                    <ChooseIdpType configTypeChosen={setConfigFlow}/>
                    <br/>
                    { configPathChosen() }
                </Card>
            )
        }
        return <FlowTemplate accessToken={accessToken}/>

    }

    return (
        <React.Fragment>
            <span id="headerAnchor"></span>
            <PageHeader className="wizardHeader">
                <PageHeaderTitle title="Identity Provider Integration" className="pf-v5-u-mb-lg"/>
                <TextContent>
                    Manage identity provider integration for your organization.{' '}
                    <Text component={TextVariants.a}
                          className={(isAllowedToConfigure || isConfigured) ? '' : "hiddenPage"}>
                        <Button variant="link" href={docsLink} target="_blank" component="a" isInline
                                icon={<ExternalLinkSquareAltIcon/>} iconPosition="right" className="pf-v5-u-ml-xs">
                            Learn more about identity provider integration
                        </Button>
                    </Text>
                </TextContent>
            </PageHeader>
            <messagesContext.Provider value={{messages, setMessages}}>
                <AlertGroup id="userNotification" isLiveRegion className="alertList">
                    {messages}
                </AlertGroup>
            </messagesContext.Provider>

            <PageSection>
                <div className={!isLoading && isConfigured ? '' : 'hiddenPage'}>
                    <ConfiguredIdp responseData={idpData} recomputeAppState={recomputeAppState}/>
                </div>
                <div>
                    <div
                        className={(!isLoading && !isConfigured && isInit && isAllowedToConfigure) ? 'initPage' : 'hiddenPage'}>
                        <IdpNotSetup startIdpSetup={() => {
                            setIsInit(false);
                        }}/>
                    </div>
                    <div
                        className={(!isLoading && !isConfigured && !isInit && isAllowedToConfigure) ? 'Page' : 'hiddenPage'}>
                        {configPathChosenInit()}
                    </div>
                    <div
                        className={(!isLoading && !isConfigured && isInit && !isAllowedToConfigure) ? 'Page' : 'hiddenPage'}>
                        <DisabledPage></DisabledPage>
                    </div>
                </div>
            </PageSection>
        </React.Fragment>
    );
}


IdpSelector.propTypes = {
    accessToken: PropTypes.string
}

export default IdpSelector;
