import React, {ReactElement, useState} from 'react';
import './configured-oidc-idp.scss'
import {IdpResponseData, OidcResponseData} from "../CommonInterfaces/idp-response-types";
import {
    ActionList,
    ActionListItem,
    Alert,
    AlertGroup,
    Button,
    ClipboardCopy,
    Form,
    FormGroup,
    Popover,
    Text,
    TextContent,
    TextVariants,
    Title
} from '@patternfly/react-core';
import HelpIcon from '@patternfly/react-icons/dist/esm/icons/help-icon';
import { useChrome } from "@redhat-cloud-services/frontend-components/useChrome";
import { getSSOBaseUrl } from "../../../Utils/general-utils";
import styles from "@patternfly/react-styles/css/components/Form/form";
import OIDCForm from "../ProviderForms/oidc-form";
import {DisableIdpModal} from "../Configured/disable-idp-modal";
import {IdpTestingModal} from "../IdpTesting/idp-testing-modal";
import {DeleteIdpModal} from "../Configured/delete-idp-modal";
import {IdpDisplayNames} from "../../IdpSelfService/Constants/idp-display-names";
import {IdpCommonMessages} from "../../IdpSelfService/Constants/idp-common-messages";
import {apiUpdateIdp} from "../../IdpSelfService/ApiClient/idp-controller";

interface Props {
    oidcData: OidcResponseData,
    recomputeAppState: () => void,
    milestoneThreeEnabled: boolean
}

const ConfiguredOidcIdp = ({ oidcData, milestoneThreeEnabled, recomputeAppState }: Props) => {
    const chrome = useChrome();
    const ssoBaseUrl = getSSOBaseUrl(chrome);
    const [isTestingModalVisible, toggleTestingModalVisibility] = useState(false);
    const [isDisabledModalVisible, toggleIsDisabledModalVisible] = useState(false);
    const [isDeleteModalVisible, toggleIsDeleteModalVisible] = useState(false);
    const [isEditing, setIsEditing] = useState(false);
    const [inputData, setInputData] = useState<OidcResponseData>(oidcData);
    const [alerts, setAlerts] = React.useState<React.ReactNode>();

    // the time in milliseconds that messages will be displayed on the page
    const messageTimeout = 12000;

    /**
     * triggered by onChange of each of the inputs, this simply updates the current state for the inputData.
     * @param editEvent the event value provided by the event.
     */
    const onEdit = (editEvent:any) => {
        if (!isEditing) {
            setIsEditing(true);
        }
        const existingInput = {...inputData};
        // @ts-ignore
        existingInput[editEvent.currentTarget.name] = editEvent.currentTarget.value;
        setInputData(existingInput)
    }

    const scrollToTop = () => {
        document.getElementById('headerAnchor')!.scrollIntoView({behavior: 'smooth'});
    }

    /**
     * throws a fresh alert component into the alert block with green background to indicate success
     * @param message the text displayed in the alert
     */
    const showSuccessMessage = (message: string) => {
        setAlerts(<Alert variant="success" title={message} timeout={messageTimeout} onTimeout={() => { setAlerts([]) }}/>);
        scrollToTop();
    }

    /**
     * throws a fresh alert component into the alert block with red background to indicate an error.
     * @param messages the text displayed in the error.
     */
    const setErrorMessage = (messages: Record<string, string>) => {
        const errorList: ReactElement[] = [];
        const keysList = Object.keys(messages);
        keysList.forEach(key => {
            errorList.push(<div key={key}><span className="bolderKey">{IdpDisplayNames.getPrintableString(key)}:</span> {messages[key]}</div>)
        })
        if (keysList.length > 0) {
            setAlerts(<Alert variant="danger" title={IdpCommonMessages.UPDATE_ERROR_TITLE}>{errorList}</Alert>);
            scrollToTop();
        }
        else {
            setAlerts([]);
        }
    }

    /**
     * submit api request to update the existing idp configuration settings.
     */
    const updateIdpSettings = () => {
        setErrorMessage({});

        // need to destructure the existing inputData
        const {
            providerId,
            issuer,
            clientId,
            clientSecret,
            authorizationUrl,
            tokenUrl,
            publicKey,
            publicKeyId
        } = inputData;

        const updateData = {
            providerId,
            issuer,
            clientId,
            clientSecret,
            authorizationUrl,
            tokenUrl,
            publicKey,
            publicKeyId
        }

        apiUpdateIdp(chrome, updateData, (responseBody: IdpResponseData) => {
            if (responseBody.error && responseBody.fields) {
                const errorFields = Array.isArray(responseBody.fields)
                    ? Object.fromEntries(responseBody.fields.map((msg, index) => [`${index}`, msg]))
                    : responseBody.fields;

                setErrorMessage(errorFields);
            } else {
                showSuccessMessage(IdpCommonMessages.UPDATE_SUCCESS_MESSAGE);
                setIsEditing(false);
                setInputData(prevData => ({
                    ...prevData,
                    lastUpdatedDate: responseBody.lastUpdatedDate,
                    lastUpdatedBy: responseBody.lastUpdatedBy
                }));
            }
        }).catch((error) => {
            console.error("Failed to update IdP:", error);
        });
    }

    /**
     *  determines the action button that should be displayed.
     */
    const actionButton = () => {
        if (milestoneThreeEnabled) {
            if (oidcData.enabled) {
                return (
                    <ActionListItem>
                    <Button variant={"danger"} onClick={() => toggleIsDisabledModalVisible(true)}>
                        Disable
                    </Button>
                    </ActionListItem>
                )
            }
            if (isEditing) {
                return (
                    <Button onClick={() => updateIdpSettings()}>
                        Update
                    </Button>
                )
            }
            return (
                <Button onClick={() => toggleTestingModalVisibility(true)}>
                    Test and enable
                </Button>
            );
        }
        return (
            <>
                <Popover
                    triggerAction="hover"
                    aria-label="Hoverable popover"
                    headerContent={<div>Read-Only</div>}
                    bodyContent={<div>This page currently shows a read-only version of your configuration. Forthcoming
                        enhancements will provide the ability to edit.</div>}
                    footerContent="">
                    <ActionListItem>
                        {oidcData.enabled
                            ? <Button variant="danger" isDisabled>
                                Disable
                            </Button>
                            : <>
                                <Button variant="primary" isDisabled>
                                    Test and enable
                                </Button>
                                <Button variant="link" isDanger isDisabled>
                                    Delete identity provider
                                </Button>
                            </>
                        }
                    </ActionListItem>
                </Popover>
            </>
        )
    }

    return (
        <Form>
            <AlertGroup id="userNotification" isLiveRegion className="alertList">
                {alerts}
            </AlertGroup>

            <OIDCForm baseData={oidcData} editableData={inputData} editFunction={onEdit} isCreation={false}/>

            <Title headingLevel={"h3"}>URLs required for identity provider configuration</Title>

            <TextContent>
                <Text component={TextVariants.p}>
                    Use these URLs to assist in completing setup within your identity provider.
                </Text>
            </TextContent>

            <FormGroup
                label="Service provider metadata URL"
                fieldId="metadata-url"
                labelIcon={
                    <Popover
                        triggerAction="hover"
                        bodyContent={
                            <div>
                                Service provider metadata used to integrate Red Hat with your identity provider.
                            </div>
                        }>
                        <Button
                            variant="plain"
                            type="button"
                            onClick={(e) => e.preventDefault()}
                            className={`customPopover ${styles.formGroupLabelHelp}`}>
                            <HelpIcon/>
                        </Button>
                    </Popover>
                }>
                <ClipboardCopy
                    type="text"
                    id="metadata-url"
                    name="metadata-url"
                    isReadOnly>
                    {`${ssoBaseUrl}.well-known/openid-configuration`}
                </ClipboardCopy>
            </FormGroup>

            <FormGroup
                label="Redirect URL / Assertion Consumption Service (ACS) URL"
                fieldId="redirect-url"
                labelIcon={
                    <Popover
                        triggerAction="hover"
                        bodyContent={
                            <div>
                                The redirect URL to use when configuring your identity provider. This represents your companys SSO Portal where users authenticate to Red Hat.
                            </div>
                        }>
                        <Button
                            variant="plain"
                            type="button"
                            onClick={(e) => e.preventDefault()}
                            className={`customPopover ${styles.formGroupLabelHelp}`}>
                            <HelpIcon/>
                        </Button>
                    </Popover>
                }>
                <ClipboardCopy
                    type="text"
                    id="redirect-url"
                    name="redirect-url"
                    isReadOnly>
                    {`${ssoBaseUrl}broker/${oidcData.idpAlias}/endpoint`}
                </ClipboardCopy>
            </FormGroup>

            <ActionList>
                <ActionListItem>
                    {actionButton()}
                </ActionListItem>
                <ActionListItem>
                    <Button variant="link" onClick={() => toggleIsDeleteModalVisible(true)} isDanger>
                        Delete identity provider
                    </Button>
                </ActionListItem>
                {isDisabledModalVisible && <DisableIdpModal idpAlias={oidcData.idpAlias} isOpen={isDisabledModalVisible}
                                                            onClose={() => toggleIsDisabledModalVisible(false)}
                                                            recomputeAppState={recomputeAppState}/>}
                {isTestingModalVisible && <IdpTestingModal idpAlias={oidcData.idpAlias} isOpen={isTestingModalVisible}
                                                           onClose={() => toggleTestingModalVisibility(false)}
                                                           recomputeAppState={recomputeAppState}/>}
                {isDeleteModalVisible && <DeleteIdpModal idpAlias={oidcData.idpAlias} isOpen={isDeleteModalVisible}
                                                         onClose={() => toggleIsDeleteModalVisible(false)}
                                                         recomputeAppState={recomputeAppState}/>}
            </ActionList>
        </Form>
    )
}

export default ConfiguredOidcIdp;
