export interface IdpRequestData {
    providerId: string;
}

export const IDENTITY_PROVIDER_ENTITY_ID = "identityProviderEntityId";
export const AUTH_REQUEST_URL = "authenticationRequestsUrl";
export const X509_CERTIFICATE = "x509Certificate";
export const SERVICE_PROVIDER_ISSUER = "serviceProviderIssuer";

export interface SamlRequestData extends IdpRequestData {
    identityProviderEntityId: string;
    authenticationRequestsUrl: string;
    x509Certificate: string;
    serviceProviderEntityId: string;
}

export interface OidcRequestData extends IdpRequestData {
    clientId: string;
    clientSecret: string;
    authorizationUrl: string;
    tokenUrl: string;
    issuer: string;
    publicKey: string;
    publicKeyId: string;
}
