import {OidcResponseData} from "@apps/IdpSelfService/CommonInterfaces/idp-response-types";
import {
    Button,
    FormGroup,
    FormHelperText,
    HelperText,
    HelperTextItem,
    Popover,
    TextInput
} from "@patternfly/react-core";
import styles from "@patternfly/react-styles/css/components/Form/form";
import HelpIcon from "@patternfly/react-icons/dist/esm/icons/help-icon";
import React from "react";
import {OidcHelperTextMessages} from "../Constants/oidc-helper-text-messages";

interface Props {
    baseData?: OidcResponseData,
    editableData: any,
    editFunction: any,
    isCreation: boolean
}

const OIDCForm = ({baseData, editFunction, editableData, isCreation}: Props) => {

    const isDisabled = () => {
        if (isCreation) {
            return false;
        } else if (baseData && baseData.enabled) {
            return true;
        }
        return false;
    }

    return (
        <div>
            <FormGroup
                label="Issuer"
                fieldId="oidc-issuer">
                <TextInput isDisabled={isDisabled()} onChange={editFunction} type="text" id="oidc-issuer" name="issuer" value={editableData.issuer} />
                <FormHelperText>
                    <HelperText>
                        <HelperTextItem>{OidcHelperTextMessages.ISSUER}</HelperTextItem>
                    </HelperText>
                </FormHelperText>
            </FormGroup>

            <FormGroup
                label="Client ID"
                fieldId="oidc-client-id">
                <TextInput isDisabled={isDisabled()} onChange={editFunction} type="text" id="oidc-client-id" name="clientId" value={editableData.clientId} />
                <FormHelperText>
                    <HelperText>
                        <HelperTextItem>{OidcHelperTextMessages.CLIENT_ID}</HelperTextItem>
                    </HelperText>
                </FormHelperText>
            </FormGroup>

            <FormGroup
                label="Client Secret"
                fieldId="oidc-client-secret">
                <TextInput isDisabled={isDisabled()} onChange={editFunction} type="text" id="oidc-client-secret" name="clientSecret" value={editableData.clientSecret} />
                <FormHelperText>
                    <HelperText>
                        <HelperTextItem>{OidcHelperTextMessages.CLIENT_SECRET}</HelperTextItem>
                    </HelperText>
                </FormHelperText>
            </FormGroup>

            <FormGroup
                label="Authorization URL"
                fieldId="oidc-authorization-url-id">
                <TextInput isDisabled={isDisabled()} onChange={editFunction} type="text" id="oidc-token-url-id" name="authorizationUrl" value={editableData.authorizationUrl} />

                <FormHelperText>
                    <HelperText>
                        <HelperTextItem>{OidcHelperTextMessages.AUTHORIZATION_URL}</HelperTextItem>
                    </HelperText>
                </FormHelperText>
            </FormGroup>

            <FormGroup
                label="Token URL"
                fieldId="authorization-url">
                <TextInput isDisabled={isDisabled()} onChange={editFunction} type="text" id="authorization-url" name="tokenUrl" value={editableData.tokenUrl}/>
                <FormHelperText>
                    <HelperText>
                        <HelperTextItem>{OidcHelperTextMessages.TOKEN_URL}</HelperTextItem>
                    </HelperText>
                </FormHelperText>
            </FormGroup>

            <FormGroup
                label="Public Key ID"
                fieldId="publicKeyId">
                <TextInput isDisabled={isDisabled()} onChange={editFunction} type="text" id="public-key-id" name="publicKeyId" value={editableData.publicKeyId} />
                <FormHelperText>
                    <HelperText>
                        <HelperTextItem>{OidcHelperTextMessages.PUBLIC_KEY_ID}</HelperTextItem>
                    </HelperText>
                </FormHelperText>
            </FormGroup>

            <FormGroup
                label="Public Key"
                fieldId="publicKey">
                <TextInput isDisabled={isDisabled()} onChange={editFunction} type="text" id="public-key" name="publicKey" value={editableData.publicKey} />
                <FormHelperText>
                    <HelperText>
                        <HelperTextItem>{OidcHelperTextMessages.PUBLIC_KEY}</HelperTextItem>
                    </HelperText>
                </FormHelperText>
            </FormGroup>
        </div>
    )
}

export default OIDCForm;
