import React, {ReactElement} from 'react';
import {Alert} from "@patternfly/react-core";
import {IdpDisplayNames} from "../Constants/idpDisplayNames";
import {IdpCommonMessages} from "../Constants/idp-common-messages";
import {errorResponseData} from "../CommonInterfaces/idp-response-types";

const scrollToTop = () => {
    document.getElementById('headerAnchor')!.scrollIntoView({behavior: 'smooth'});
}

export const showErrorMessage = (messages: errorResponseData | undefined, setMessages: Function): void => {
    if (messages === undefined) {
        return;
    }
    const errorList: ReactElement[] = [];
    const keysList = Object.keys(messages!);
    keysList.forEach(key => {
        errorList.push(<div key={key}><span className="bolderKey">{IdpDisplayNames.getPrintableString(key)}:</span> {messages[key]}</div>)
    })
    if (keysList.length > 0) {
        setMessages(<Alert variant="danger" title={IdpCommonMessages.UPDATE_ERROR_TITLE}>{errorList}</Alert>);
        scrollToTop();
    }
    else {
        setMessages([]);
    }
}

// the time in milliseconds that messages will be displayed on the page
const messageTimeout = 12000;

/**
 * throws a fresh alert component into the alert block with green background to indicate success
 * @param message the text displayed in the alert
 * @param setMessages the setter function that needs to be passed from the provided context
 */
export const showSuccessMessage = (message: string, setMessages: Function) => {
    setMessages(<Alert variant="success" title={message} timeout={messageTimeout} onTimeout={() => { setMessages([]) }}/>);
    scrollToTop();
}
