import React from 'react';
import {
    Card,
    CardBody,
    CardHeader,
    Gallery,
    Title,
    CardTitle,
    Text, Label
} from "@patternfly/react-core";
import {FlowStep} from '../Constants/flow-step';

interface Props {
    configTypeChosen: Function
}

const ChooseIdpType = ({configTypeChosen}: Props) => {

    const [isChecked, setIsChecked] = React.useState('');
    const samlId = 'saml-card-input';
    const oidcId = 'oidc-card-input';

    const onChange = (event: React.FormEvent<HTMLInputElement>) => {
        if (event.currentTarget.id === samlId) {
            configTypeChosen(FlowStep.SAML_CONFIG);
        } else {
            configTypeChosen(FlowStep.OIDC_CONFIG);
        }

        setIsChecked(event.currentTarget.id);
    }

    return (

        <CardBody>
            <Title headingLevel="h1" size="2xl">Set up an identity provider</Title>
            <Text className="pf-v5-u-mt-lg pf-v5-u-text-color-required">All fields are required.</Text>
            <Title headingLevel="h2" size="lg" className="pf-v5-u-mt-lg">Choose an identity provider protocol</Title>
            <br/>
            <Gallery hasGutter minWidths={{
                default: '49%'
            }}>
                    <Card id="saml-card" isSelectable isClickable isClicked={isChecked === samlId} isSelected={isChecked === samlId}>
                        <CardHeader className="pf-v5-u-pt-sm"
                            selectableActions={{
                                selectableActionId: samlId,
                                selectableActionAriaLabelledby: 'saml-card',
                                name: 'selected-card',
                                variant: 'single',
                                onChange,
                                hasNoOffset: true
                            }}>
                            <CardTitle className="pf-v5-u-mt-sm">SAML 2.0   <Label color="green" className="pf-v5-u-ml-md">Suggested</Label></CardTitle>
                        </CardHeader>
                        <CardBody>
                            <p>SAML is often used with legacy systems due to its mature specifications and extensive support for complex identity federation scenarios.</p>
                        </CardBody>
                    </Card>
                 <Card id="oidc-card" isSelectable isClickable isClicked={isChecked === oidcId} isSelected={isChecked === oidcId}>
                        <CardHeader className="pf-v5-u-pt-sm"
                            selectableActions={{
                                selectableActionId: oidcId,
                                selectableActionAriaLabelledby: 'oidc-card',
                                name: 'selected-card',
                                variant: 'single',
                                onChange,
                                hasNoOffset: true
                            }}>
                            <CardTitle className="pf-v5-u-mt-sm">OpenID Connect</CardTitle>
                        </CardHeader>
                        <CardBody>
                            <p>OIDC is a modern, lightweight and flexible standard that simplifies configuration enabling easier use across diverse platforms.</p>
                        </CardBody>
                    </Card>
            </Gallery>
        </CardBody>
    )
}

ChooseIdpType.propTypes = {
    configTypeChosen: Function
}


export default ChooseIdpType;
