import React, {useState, useContext} from 'react';
import './configured-saml-idp.scss'
import {errorResponseData, IdpResponseData, SamlResponseData} from "../CommonInterfaces/idp-response-types";
import {
    ActionList,
    ActionListItem,
    Button,
    Form,
    Text,
    TextContent,
    TextVariants,
    Title
} from '@patternfly/react-core';
import { useChrome } from "@redhat-cloud-services/frontend-components/useChrome";
import { getSSOBaseUrl } from "../../../Utils/general-utils";
import { IdpTestingModal } from "../IdpTesting/idp-testing-modal";
import { DisableIdpModal } from "../Configured/disable-idp-modal";
import { DeleteIdpModal } from './delete-idp-modal';
import {apiUpdateIdp} from "../ApiClient/idp-controller";
import SAMLForm from '../ProviderForms/saml-form';
import SamlStaticContent from "../ProviderForms/saml-static-content";
import messagesContext from "../CommonInterfaces/messages-context";
import {showSuccessMessage, showErrorMessage} from '../CommonInterfaces/messaging-functions';
import {IdpCommonMessages} from "../Constants/idp-common-messages";

interface Props {
    samlData: SamlResponseData,
    recomputeAppState: () => void
}

const ConfiguredSamlIdp = ({ samlData, recomputeAppState }: Props) => {
    const chrome = useChrome();
    const ssoBaseUrl = getSSOBaseUrl(chrome);
    const [isTestingModalVisible, toggleTestingModalVisibility] = useState(false);
    const [isDisabledModalVisible, toggleIsDisabledModalVisible] = useState(false);
    const [isDeleteModalVisible, toggleIsDeleteModalVisible] = useState(false);
    const [isEditing, setIsEditing] = useState(false);
    const [inputData, setInputData] = useState<SamlResponseData>(samlData);

    const {setMessages} = useContext(messagesContext);

    /**
     * triggered by onChange of each of the inputs, this simply updates the current state for the inputData.
     * @param editEvent the event value provided by the event.
     */
    const onEdit = (editEvent:any) => {
        if (!isEditing) {
            setIsEditing(true);
        }
        const existingInput = {...inputData};
        // @ts-ignore
        existingInput[editEvent.currentTarget.name] = editEvent.currentTarget.value;
        setInputData(existingInput)
    }


    /**
     * submit api request to update the existing idp configuration settings.
     */
    const updateIdpSettings = () => {
        showErrorMessage([] as unknown as errorResponseData, setMessages);

        // need to destructure the existing inputData
        const {
            providerId,
            identityProviderEntityId,
            authenticationRequestsUrl,
            serviceProviderEntityId,
            x509Certificate
        } = inputData;

        const updateData = {
            providerId,
            identityProviderEntityId,
            authenticationRequestsUrl,
            serviceProviderEntityId,
            x509Certificate
        }

        apiUpdateIdp(chrome, updateData, (responseBody: IdpResponseData) => {
            // show some form of toast indicating that the update was successful.
            if (responseBody.error) {
                showErrorMessage(responseBody.fields as unknown as errorResponseData, setMessages);
            }
            else {
                showSuccessMessage(IdpCommonMessages.UPDATE_SUCCESS_MESSAGE, setMessages);
                // after a successful update be sure to reset the editing state
                setIsEditing(false);
                // update the last updated messages
                const existingInput = {...inputData};
                existingInput.lastUpdatedDate = responseBody.lastUpdatedDate
                existingInput.lastUpdatedBy = responseBody.lastUpdatedBy;
                setInputData(existingInput)
            }
        });
    }

    /**
     *  determines the action button that should be displayed.
     */
    const actionButton = () => {
        if (samlData.enabled) {
            return (
                <Button variant={"danger"} onClick={() => toggleIsDisabledModalVisible(true)}>
                    Disable
                </Button>
            )
        }
        if (isEditing) {
            return (
                <Button onClick={() => updateIdpSettings()}>
                    Update
                </Button>
            )
        }
        return (
            <Button onClick={() => toggleTestingModalVisibility(true)}>
                Test and enable
            </Button>
        )
    }

    return (
        <Form>

            {/* Render default SAML form components */}
            <SAMLForm baseData={samlData} editableData={inputData} editFunction={onEdit} />

            {/* Statically generated content specific for configured SAML */}
            <Title headingLevel={"h3"}>URLs required for identity provider configuration</Title>

            <TextContent>
                <Text component={TextVariants.p}>
                    Use these URLs to assist in completing setup within your identity provider.
                </Text>
            </TextContent>

            <SamlStaticContent baseUrl={ssoBaseUrl} idpAlias={samlData.idpAlias} />

            <ActionList>
                <ActionListItem>
                    {actionButton()}
                </ActionListItem>
                <ActionListItem>
                    <Button variant="link" onClick={() => toggleIsDeleteModalVisible(true)} isDanger>
                        Delete identity provider
                    </Button>
                </ActionListItem>
                {isDisabledModalVisible && <DisableIdpModal idpAlias={samlData.idpAlias} isOpen={isDisabledModalVisible}
                                                            onClose={() => toggleIsDisabledModalVisible(false)}
                                                            recomputeAppState={recomputeAppState}/>}
                {isTestingModalVisible && <IdpTestingModal idpAlias={samlData.idpAlias} isOpen={isTestingModalVisible}
                                                           onClose={() => toggleTestingModalVisibility(false)}
                                                           recomputeAppState={recomputeAppState}/>}
                {isDeleteModalVisible && <DeleteIdpModal idpAlias={samlData.idpAlias} isOpen={isDeleteModalVisible}
                                                         onClose={() => toggleIsDeleteModalVisible(false)}
                                                         recomputeAppState={recomputeAppState}/>}
            </ActionList>
        </Form>
    )
}

export default ConfiguredSamlIdp;
