import {
    Button,
    FormGroup,
    FormHelperText,
    HelperText,
    HelperTextItem,
    Popover, TextArea,
    TextInput
} from "@patternfly/react-core";
import {SamlHelperTextMessages} from "../Constants/saml-helper-text-messages";
import styles from "@patternfly/react-styles/css/components/Form/form";
import HelpIcon from "@patternfly/react-icons/dist/esm/icons/help-icon";
import React from "react";
import {SamlResponseData} from "../CommonInterfaces/idp-response-types";

interface Props {
    baseData?: SamlResponseData,
    editableData: any,
    editFunction: any,
}

const SAMLForm = ({baseData, editFunction, editableData}: Props) => {
    return (
        <div>
            <FormGroup
                label="Identity provider Entity ID"
                fieldId="saml-entity-id"
                labelIcon={
                    <Popover
                        triggerAction="hover"
                        bodyContent={
                            <div>{SamlHelperTextMessages.ENTITY_ID}</div>
                        }>
                        <Button
                            variant="plain"
                            type="button"
                            onClick={(e) => e.preventDefault()}
                            className={`customPopover ${styles.formGroupLabelHelp}`}>
                            <HelpIcon />
                        </Button>
                    </Popover>
                }>
                <TextInput isDisabled={baseData && baseData.enabled} onChange={editFunction} type="text" id="saml-entity-id" name="identityProviderEntityId" value={editableData.identityProviderEntityId} />
                <FormHelperText>
                    <HelperText>
                        <HelperTextItem>{SamlHelperTextMessages.ENTITY_ID}</HelperTextItem>
                    </HelperText>
                </FormHelperText>
            </FormGroup>

            <FormGroup
                label="Single sign-on authentication request URL"
                fieldId="saml-authn-request-url">
                <TextInput isDisabled={ baseData && baseData.enabled} onChange={editFunction} type="text" id="saml-authn-request-url" name="authenticationRequestsUrl" value={editableData.authenticationRequestsUrl} />
                <FormHelperText>
                    <HelperText>
                        <HelperTextItem>{SamlHelperTextMessages.AUTHENTICATION_REQUEST_URL}</HelperTextItem>
                    </HelperText>
                </FormHelperText>
            </FormGroup>

            <FormGroup
                label="Service provider issuer"
                fieldId="saml-provider-issuer">
                <TextInput isDisabled={ baseData && baseData.enabled} onChange={editFunction} type="text" id="saml-provider-issuer" name="serviceProviderEntityId" value={editableData.serviceProviderEntityId} />
                <FormHelperText>
                    <HelperText>
                        <HelperTextItem>{SamlHelperTextMessages.SERVICE_PROVIDER_ISSUER}</HelperTextItem>
                    </HelperText>
                </FormHelperText>
            </FormGroup>

            <FormGroup
                label="x509 certificate"
                fieldId="saml-x509-certificate"
                labelIcon={
                    <Popover
                        triggerAction="hover"
                        bodyContent={
                            <div>A PEM certificate that is used to check for signatures.</div>
                        }>
                        <Button
                            variant="plain"
                            type="button"
                            onClick={(e) => e.preventDefault()}
                            className={`customPopover ${styles.formGroupLabelHelp}`}>
                            <HelpIcon />
                        </Button>
                    </Popover>
                }>
                <TextArea resizeOrientation="vertical" isDisabled={baseData && baseData.enabled} type="text" onChange={editFunction} id="saml-authn-request-url" name="x509Certificate" value={editableData.x509Certificate} />
                <FormHelperText>
                    <HelperText>
                        {SamlHelperTextMessages.X509_CERTIFICATE.map((message, index) => (
                            <HelperTextItem key={index}>
                                <p dangerouslySetInnerHTML={{__html: message}}/>
                            </HelperTextItem>
                        ))}
                    </HelperText>
                </FormHelperText>
            </FormGroup>
        </div>
    )
}

export default SAMLForm;
