import './idp-not-setup.scss'
import React from 'react';
import {
    Title,
    Text,
    Button, Icon
} from '@patternfly/react-core';
import CubesIcon from '@patternfly/react-icons/dist/esm/icons/cubes-icon';
import InfoCircleIcon from '@patternfly/react-icons/dist/esm/icons/info-circle-icon';
import PropTypes from "prop-types";

interface Props{
    startIdpSetup: Function,
}

const IdpNotSetup = ({startIdpSetup}: Props) => {

    const handleButtonClick = () => {
        startIdpSetup();
    }

    /**
     * this is just a conditional rendering function that determines if we display the init page or the selection page
     */
    const renderStep = () => {
        return (
            <div className="notSetupDiv">
                <CubesIcon className="cubesIcon"/>
                <Title headingLevel="h2" size="xl" className="pf-v5-u-mt-lg">No Identity Providers are currently
                    configured</Title>
                <Text className="pf-v5-u-mt-md">Your organization has not integrated an identity provider. More
                    information is available <Button variant="link" target="_blank"
                                                     href="https://access.redhat.com/articles/7093475" component="a"
                                                     isInline>here</Button>.</Text>
                <Text><Icon status="info" isInline><InfoCircleIcon/></Icon> Configure your identity provider, test and
                    enable immediately, or return and enable when you&apos;re ready.</Text>
                <Button className="pf-v5-u-mt-md" variant="primary" onClick={() => handleButtonClick()}>Integrate an identity provider</Button>
            </div>
        )
    }

    return renderStep();
}

IdpNotSetup.propTypes = {
    configChosen: PropTypes.func
}

export default IdpNotSetup;
