import './configured-idp.scss';
import ConfiguredOidcIdp from './configured-oidc-idp';
import ConfiguredSamlIdp from './configured-saml-idp';
import React, {useState, useEffect, ReactElement} from 'react';
import {
    Alert,
    Card,
    CardBody, Form, FormGroup, Label,
    Spinner, Title
} from '@patternfly/react-core';

import {IdpResponseData, OidcResponseData, SamlResponseData} from "../CommonInterfaces/idp-response-types";
import {useFlag} from "@unleash/proxy-client-react";
import CheckCircleIcon from "@patternfly/react-icons/dist/esm/icons/check-circle-icon";
import WrenchIcon from "@patternfly/react-icons/dist/esm/icons/wrench-icon";
import AuditInfo from "../../../Components/AuditInfo/audit-info";

interface Props {
    responseData: IdpResponseData,
    recomputeAppState: () => void
}

const ConfiguredIdp = ({responseData, recomputeAppState}: Props) => {

    // flag to control which values get displayed
    const isM3Enabled = useFlag('iamconsole.IDPIntegration.m3');

    const [providerType, setProviderType] = useState<ReactElement>(
       <Spinner/>
    )

    useEffect(() => {
        if (responseData.providerId === 'saml-improved') {
            setProviderType(<ConfiguredSamlIdp samlData={responseData as SamlResponseData} recomputeAppState={recomputeAppState}/>);
        }
        else if (responseData.providerId === 'oidc-improved') {
            setProviderType(<ConfiguredOidcIdp oidcData={responseData as OidcResponseData} milestoneThreeEnabled={isM3Enabled} recomputeAppState={recomputeAppState}/>);
        }
    }, [responseData]);

    return (
        <React.Fragment>
            <Card isCompact={false} isLarge={true}>
                <CardBody>
                    <Form>
                        { !isM3Enabled && responseData.providerId === 'oidc-improved' &&
                            <Alert variant="info" isInline title="This page currently shows a read-only version of your configuration.">
                                <p>Forthcoming enhancements will provide the ability to edit.</p>
                            </Alert>
                        }

                        <Title headingLevel={"h1"} className="pf-v5-u-display-flex">
                            {responseData.displayName}
                            {responseData.enabled
                                ? <Label color="green" icon={<CheckCircleIcon />} className="pf-v5-u-ml-md">
                                    Enabled
                                </Label>
                                : <Label color="grey" icon={<WrenchIcon />} className="pf-v5-u-ml-md">
                                    Disabled
                                </Label>
                            }
                        </Title>

                        { (responseData.createdDate || responseData.lastUpdatedDate) && (
                            <FormGroup fieldId="audit-data">
                                { responseData.createdDate && (
                                    <AuditInfo preValue={'Created on'} value={new Date(responseData.createdDate).toLocaleDateString()}/>
                                )}
                                { responseData.lastUpdatedDate && (
                                    <AuditInfo preValue={'Last updated on'} value={new Date(responseData.lastUpdatedDate).toLocaleTimeString()} postValue={'by ' + responseData.lastUpdatedBy}/>
                                )}
                            </FormGroup>
                        )}

                        { responseData.enabled && responseData.providerId === "saml-improved" &&
                            <Alert variant="info" isInline title="Disable your configuration to edit fields." />
                        }
                        {providerType}
                    </Form>
                </CardBody>
            </Card>
        </React.Fragment>
    )
}

export default ConfiguredIdp;
